* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
}

span {
  display: inline-block;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4 {
  text-transform: inherit;
}

a {
  text-decoration: none;
  display: inline-block;
}

a:hover {
  text-decoration: underline;
}

li {
  list-style: none;
  list-style-position: inside;
}

img,
svg {
  display: block;
  max-width: 100%;
}

.container-wrap {
  padding: 1rem;
}

@media (min-width: 1232px) {
  .container-wrap {
    width: 1216px;
    margin: 0 auto;
    padding: 0;
  }
}
.form-template br {
  display: none;
}

.form-template span.wpcf7-form-control-wrap {
  display: block;
  margin-bottom: 30px;
}

.form-template span.wpcf7-form-control-wrap:last-child {
  margin-bottom: 0;
}

.form-template input[type=text],
.form-template input[type=email],
.form-template input[type=tel] {
  background: #EFF0F7;
  border-radius: 16px;
  border: none;
  color: #14142B;
  border: 2px solid transparent;
  font-size: 13px;
  display: block;
  padding: 1rem 1.5rem;
  box-sizing: border-box;
  width: 100%;
  font-weight: 400;
  box-sizing: border-box;
}

.form-template input[type=text] :-ms-input-placeholder,
.form-template input[type=email] :-ms-input-placeholder,
.form-template input[type=tel] :-ms-input-placeholder {
  color: #6E7191;
  font-size: 15px;
  font-weight: 400;
}

.form-template input[type=text] ::placeholder,
.form-template input[type=email] ::placeholder,
.form-template input[type=tel] ::placeholder {
  color: #6E7191;
  font-size: 15px;
  font-weight: 400;
}

.form-template input[type=text] :focus,
.form-template input[type=email] :focus,
.form-template input[type=tel] :focus {
  border-color: #14142B;
}

.form-template input[type=text].wpcf7-not-valid,
.form-template input[type=email].wpcf7-not-valid,
.form-template input[type=tel].wpcf7-not-valid {
  background-color: #FFECFC;
  border-color: #CA024F;
}

.form-template .two-inputs {
  margin-bottom: 30px;
}

.form-template .wpcf7-not-valid-tip {
  display: none;
}

.form-template .button-step-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  gap: 2rem;
  margin-top: 40px;
}

.form-template .button-step-form .steps-itens {
  display: -ms-flexbox;
  display: flex;
  gap: 2rem;
  position: relative;
  z-index: 1;
}

.form-template .button-step-form .steps-itens .step--item {
  background-color: #D9D9D9;
  color: #4E4B66;
  width: 39px;
  height: 39px;
  border-radius: 50%;
  font-size: 15px;
  line-height: 24px;
  letter-spacing: 0.75px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
  transition: background-color 0.5s ease, color 0.5s ease;
  position: relative;
}

.form-template .button-step-form .steps-itens .step--item:after {
  content: "";
  position: absolute;
  top: 50%;
  right: -33px;
  transform: translateY(-50%);
  background-color: #D9D9D9;
  height: 6px;
  width: 2.5rem;
  z-index: -1;
}

.form-template .button-step-form .steps-itens .step--item:last-child:after {
  display: none;
}

.form-template .button-step-form .steps-itens .step--item.form-complete, .form-template .button-step-form .steps-itens .step--item:hover {
  background-color: #27AF00;
  color: #FCFCFC;
}

.form-template .button-step-form .button-form-steps {
  background-color: #BCFAA1;
  border-color: #BCFAA1;
  color: #27AF00;
  font-weight: 600;
  font-size: 13px;
  line-height: 22px;
  letter-spacing: 0.25px;
  padding: 9px 23.5px;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.form-template .button-step-form .button-form-steps:after {
  content: "";
  background-image: url("../../assets/images/svg/icon-arrow-right-green.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 13px;
  height: 13px;
  position: relative;
  margin-left: 3px;
  display: inline-block;
  transition: transform 0.5s ease;
}

.form-template .button-step-form .button-form-steps:hover:after {
  transform: translateX(0.5rem);
}

.step-container {
  display: none;
}

.step-container.active {
  display: block;
}

@media (min-width: 992px) {
  .form-template .two-inputs {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    gap: 1rem;
  }
  .form-template .two-inputs span {
    margin: 0;
  }
}
.placeholder {
  position: relative;
}

.placeholder .placeholder--text {
  color: #6E7191;
  font-size: 15px;
  font-weight: 400;
  position: absolute;
  top: 33.6%;
  left: 26px;
  display: inline-block;
  transition: top 0.5s ease, font-szie 0.4s ease;
  pointer-events: none;
}

.placeholder.focus .placeholder--text {
  font-size: 11px;
  top: 15.2%;
  transition: top 0.5s ease, font-szie 0.4s ease;
}

.placeholder input {
  padding: 1.5rem 1.5rem 0.8rem !important;
}

.button {
  padding: 20px 32px;
  border: 2px solid;
  box-sizing: border-box;
  border-radius: 16px;
  font-weight: 600;
  font-size: 18px;
  line-height: 32px;
  transition: background-color 0.5s ease, color 0.5s ease;
}

.button:hover {
  text-decoration: none;
}

.button--white {
  background-color: #FCFCFC;
  border-color: #fcfcfc;
  color: #14142B;
}

.button--white:hover {
  background-color: transparent;
  color: #FCFCFC;
}

.button-border--white {
  background-color: transparent;
  border-color: #fcfcfc;
  color: #fcfcfc;
}

.button-border--white:hover {
  background-color: #fcfcfc;
  color: #14142B;
}

.button-green {
  color: #27AF00;
  background: #BCFAA1;
  border-radius: 8px;
  border-color: #BCFAA1;
}

.grid {
  display: -ms-grid;
  display: grid;
}

@media (min-width: 992px) {
  .two_grids {
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
  }
  .three_grids {
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
  }
  .four_grids {
    -ms-grid-columns: (1fr)[4];
        grid-template-columns: repeat(4, 1fr);
  }
  .six_grids {
    -ms-grid-columns: (1fr)[3];
        grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1232px) {
  .four-grids {
    -ms-grid-columns: (1fr)[4];
        grid-template-columns: repeat(4, 1fr);
  }
  .five_grids {
    -ms-grid-columns: (1fr)[5];
        grid-template-columns: repeat(5, 1fr);
  }
  .six-grids {
    -ms-grid-columns: (1fr)[6];
        grid-template-columns: repeat(6, 1fr);
  }
}
.header {
  min-height: 92px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #FCFCFC;
}

@media (min-width: 992px) {
  .header-middle_container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}
.menu .menu-item a {
