    display: block;
  }
}
.menu-desk .menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  gap: 2rem;
}

.footer-menu .menu-item a {
  color: #4E4B66;
  text-decoration: none;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  letter-spacing: 0.75px;
}

.footer {
  margin-top: 4rem;
}

.footer-top_container {
  gap: 2rem;
}

.footer--item .logo_container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  max-width: 248px;
}

.footer--item .redes-sociais_container {
  margin-top: 3rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  gap: 2rem;
